import "./App.scss";
import React, { useState } from "react";
import Message, {
  MessageProps,
  MessageSide,
  MessageType,
} from "./components/message/Message";
import TextField from "@mui/material/TextField";
import { Avatar, Button } from "@mui/material";
import { ThemeProvider, createTheme } from "@mui/material/styles";
import CssBaseline from "@mui/material/CssBaseline";
import crow from "./images/crow.png";
// import png

const darkTheme = createTheme({
  palette: {
    mode: "dark",
  },
});

type RwkvState = [number[], number[], number[], number[]] | undefined;

const mess: MessageProps[] = [
  { text: "Hello", type: MessageType.Success, side: MessageSide.Left },
  { text: "World", type: MessageType.Error, side: MessageSide.Right },
  { text: "!", type: MessageType.Info },
];

function App() {
  const [messages, setMessages] = useState(mess);
  const [state, setState] = useState<RwkvState>(undefined);
  const [currentMessage, setCurrentMessage] = useState("");

  const [server, setServer] = useState("http://localhost:3001/");

  const getMessage = async (message: string, state: RwkvState) => {
    // do a fetch to the server
    const data = await fetch(server, {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        message: message,
        state: state,
      }),
    });

    const datajson: { message: string; state: RwkvState } = await data.json();

    setState(datajson.state);
    setMessages([
      ...messages,
      {
        text: message,
        icon: "data:image/png;base64,",
      },
      { text: datajson.message, side: MessageSide.Right, icon: crow },
    ]);
  };

  const onInputChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setCurrentMessage(event.target.value);
  };

  const onServerChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setServer(event.target.value);
  };

  const onSend = () => {
    setMessages([
      ...messages,
      {
        text: currentMessage,
        icon: "data:image/png;base64,",
      },
    ]);
    getMessage(currentMessage, state);
    setCurrentMessage("");
  };

  return (
    <ThemeProvider theme={darkTheme}>
      <CssBaseline />
      <div className="App">
        <Avatar
          src={crow}
          sx={{
            width: 64,
            height: 64,
            position: "absolute",
            left: 32,
            top: 32,
            filter: "invert(1)",
          }}
        />
        <h1>Chat-RWKV</h1>
        <div className="message-box">
          {messages
            .map((message, index) => (
              <Message
                key={index}
                {...{ ...message, index: messages.length - index }}
              />
            ))
            .reverse()}
        </div>

        <TextField
          id="serverSet"
          label="Server"
          variant="outlined"
          value={server}
          onChange={onServerChange}
          color="primary"
          sx={{
            position: "absolute",
            right: 32,
            top: 32,
          }}
        />

        <TextField
          id="outlined-basic"
          multiline
          label="Message"
          variant="outlined"
          fullWidth
          value={currentMessage}
          onChange={onInputChange}
          color="primary"
          InputProps={{
            endAdornment: (
              <Button variant="contained" onClick={onSend}>
                Send
              </Button>
            ),
          }}
        />
      </div>
    </ThemeProvider>
  );
}

export default App;
